.body {
  color: #060b35;
  margin: 0;
}

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  height: 60px;
  margin-left: 20px;
  list-style: none;
  position: relative;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #060b35;
  white-space: nowrap;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition-duration: .3s;
  display: block;
}

.nav__link--is-active, .nav__link:hover {
  color: #00acdc;
}

.nav__link--is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.card__title {
  text-align: left;
  padding-inline: 16px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition-duration: .3s;
  position: absolute;
  top: 206px;
}

.card__title:hover {
  color: #34568b;
}

.card__product-code {
  color: #616070;
  text-align: left;
  padding-inline: 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  position: absolute;
  top: 246px;
}

.card__price {
  justify-content: space-between;
  width: 166px;
  padding-inline: 16px;
  display: flex;
  position: absolute;
  top: 316px;
}

.card__price-text {
  color: #616070;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-price {
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  padding-inline: 16px;
  display: flex;
}

.card__buy {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  align-content: center;
  width: 166px;
  height: 40px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition-duration: .3s;
  position: absolute;
  top: 350px;
}

.card__buy:hover {
  cursor: pointer;
  box-sizing: border-box;
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.main__card {
