  background-image: url("imac.202c52ff.jpeg");
  background-position: 19px 32px;
  background-repeat: no-repeat;
  background-size: 160px 134px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  transition-duration: .3s;
  position: relative;
}

.main__card:hover {
  transform: scale(1.2);
}

.stars {
  justify-content: space-between;
